@use '../base/mixins' as m;
@use '../base/variables' as v;

.footer {
    background-color: v.$primario;

    .copyright {
        margin: 0;
        text-align: center;
        font-size: 2.2rem;
        color: v.$blanco;
        padding: 2rem 0;
    }
}

.footer-grid {
    padding: 5rem;
    @include m.tablet {
        @include m.grid( 3, 2rem );
    }

    .footer-box {
        margin-bottom: 5rem;
        text-align: center;

        @include m.tablet {
            margin-bottom: 0;
        }

        h3 {
            color: v.$secundario;
            font-size: 2.4rem;
            margin: 0 0 1rem 0;
        }
    
        p,
        a {
            font-family: v.$fuenteCursiva;
            margin: 0;
            font-size: 2rem;
        }
    }
}