@use '../base/mixins' as m;
@use '../base/variables' as v;
.nuestro-menu {
    background-image: url(../img/menu_arriba.svg),url(../img/menu_abajo.svg) ,linear-gradient(to top, v.$primario, v.$primario);
    background-repeat: no-repeat;
    background-position: center 5rem,center 93%, 100%;
    background-size: 180%, 150%;
    padding: 15rem 2rem 30rem;

    @include m.tablet {
        background-size: 100%;
        padding: 15rem 0rem 28rem;
        background-position: center 5rem,center 90%, 100%;
    }

    @include m.desktop {
        background-size: 50%, 50% ,100%;
    }
}

.menu-contenido {

    @include m.tablet {
        @include m.grid( 2, 2rem );
    }
    
    
}

.menu {

    h3 {
        color: v.$blanco;
    }

    li {
        display: flex;
        justify-content: space-between;
    }

    p {
        color: v.$blanco;
        font-size: 2rem;
        line-height: 1.4;
        margin: 0 0 2rem 0;

        span {
            display: block;
            color: v.$secundario;
        }
    }

    .precio {
        font-size: 3.5rem;
        font-weight: 900;
    }
}